*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root{
    --white: #fff;
    --black: #000;
    --main: rgba(77, 13, 68, 0.884);
    --lightblack: #2e2d2c;
}
body{
    font-family: 'Open Sans', sans-serif;
}
.container{
    width: 100%;
    margin: 1px auto;
}

.nav{
    width: 100%;
    height: 70px;
    box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 0;
    background: rgb(193, 221, 192);
    z-index: 1;
}
.nav__container{
    width: 95%;
    display: flex;
    align-items: center;
    height: 70px;
    justify-content: space-between;
}
 .nav__left {
    padding-left: 20px;
}
.imagelogo{
    border-radius: 50%;
}
.header{
    width: 100%;
    height: 15vh;
    
}
a{
    text-decoration: none;
}
.cart-icon{
    color: var(--main);
    font-size: 30px;
}
.basket{
    position: relative;
}
.basket span{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: -20px;
    top: -10px;
    width: 25px;
    height: 25px;
    background: var(--lightblack);
    border-radius: 50%;
    color: var(--white);
}
.mtb-30{
    margin: 30px auto;
}
.mt-100{
    margin-top: 100px;
}
.row{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}
.col-2{
    width: 16.33333333333333%;
}
.col-3{
    width: 20%;
}
.col-6{
    width: 50%;
}
.col-9{
    width: 75%;
}
.product{
    padding: 25px;
    width: 100%;
    overflow: hidden;
}
.product__img{
    width: 100%;
    height: 240px;
    overflow: hidden;
}
.product__img img{
    width: 100%;
    height: 100%;
    transition: all .5s linear;
}
.product__img img:hover{
    transform:scale(1.2);

}
.product__name{
    margin: 10px 0;
    text-transform: capitalize;
    color: #707070;
}
.product__discount__price{
    text-align: right;
    font-size: 16px;
}
.product__price .discount{
    color:silver;
    font-weight: 400;
    font-size: 12px;
    padding-left: 5px;
}
.actualPrice{
    text-decoration: line-through;
    font-size: 16px;
}
.details__image{
    width: 50%;
    height: auto;
    padding-right: 25px;
    padding-left: 25px;
}
.details__image img {
    width: 100%;
    height: auto;
    margin-bottom: 30px;
    
}
.details__name{
    font-size: 25px;
    color: #2c2c2c;
    font-weight: 400;
    text-transform: capitalize;
}
.details__actual{
    text-decoration: line-through;
    font-size: 20px;
}
.details__discount{
    margin-left: 50px;
    font-size: 20px;

}
.details__price{
    margin: 20px 0;
}
.details__incDec{
    display: flex;
}
.dec,
.inc,
.quantity{
    width: 60px;
    height: 50px;
    border: 1px solid silver;
    display: flex;
    align-items: center;
    justify-content: center;
}
.quantity{
    border-left: none;
    border-right: none;
}
.dec,
.inc{
    cursor: pointer;
}
.btn-default{
    margin-left: 10px;
    width: 100%;
    border: none;
    background: var(--main);
    color: var(--white);
    outline: none;
    font-size: 20px;
    text-transform: uppercase;
    cursor: pointer;
}
.details__p{
    margin-top: 15px;
}
.cart{
    margin-top: 100px;
}
.cart h3{
    font-weight: 400;
}
.cart__heading{
    background: #f4f6f8;
    padding: 10px;
}
.cart__image img{
    width: 80px;
    height: 80px;
    object-fit: contain;
}
.verticalAllign{
    align-items: center;
    margin-top: 10px;
}
.text__center{
    text-align: center;
}
.cart__remove {
	cursor: pointer;
}
.summary {
	padding-left: 10px;
}
.summary__heading {
	background: #f4f6f8;
	padding: 10px;
}
.summary__details {
	background: var(--main);
	color: var(--white);
	padding: 15px;
}
.mb-10 {
	margin-bottom: 10px;
}
.checkout {
	border: none;
	cursor: pointer;
	outline: none;
	background: var(--white);
	color: var(--main);
	width: 100%;
	padding: 12px 5px;
	font-size: 17px;
	text-transform: uppercase;
}
.checkout1 {
	border: none;
	outline: none;
	color: var(--main);
	width: 100%;
	padding: 20px 60px;
	font-size: 17px;
	text-transform: uppercase;
}

@media (max-width: 765px) {
	.container {
		width: 100%;
		padding: 0 30px;
	}
	.col-3 {
		width: 50%;
	}
	.col-2 {
		width: 100%;
		text-align: center;
		padding: 5px;
	}
	.col-6 {
		width: 100%;
	}
	.col-9 {
		width: 100%;
	}
	.details__image {
		padding-right: 0;
	}
	.cart__heading {
		display: none;
	}
	.summary-col {
		width: 100% !important;
	}
	.cart__incDec {
		display: flex;
		justify-content: center;
	}
	.cart h3 {
		text-align: center;
	}
}
@media (max-width: 400px) {
	.col-3 {
		width: 100%;
	}
	.col-2 {
		width: 100%;
	}
	.col-6 {
		width: 100%;
	}
	.details__image {
		padding-right: 0;
	}
    
}










